html, body {
  height: 100%;
  overflow-x: hidden;
}

body {
  font-family: $font-regular;
  font-size: 16px;
  -webkit-overflow-scrolling: touch !important;
  &.modal-open {
        overflow: hidden !important;
        position: fixed;
        width: 100%;
        &:before {
            content: '';
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-color: rgba(black, 0.5);
            position: fixed;
            z-index: 99;
            // @include zindex(50);
        }
    }

  @include respond-to(320) {
    font-size: 14px;
  }
  @include respond-to(480) {
    font-size: 16px;
  }
}

.common {
  padding: $common-indent $common-indent 0;
}

h1 {
  margin: 0;
  padding: 0 0 .9em 0;
  font-size: $font-size-h1 + em;
  font-weight: normal;
  font-family: 'helveticaneuecyrmedium';
}

h2 {
  margin: 0;
  padding: 0 0 0.938em 0;
  font-size: $font-size-h2 + em;
  font-weight: normal;
  font-family: 'helveticaneuecyrmedium';
}

h3 {
  margin: 0;
  padding: 0 0 1.125em 0;
  font-size: $font-size-h3 + em;
  font-weight: normal;
  font-family: 'helveticaneuecyrbold';
}

b {
  font-weight: normal;
  font-family: 'helveticaneuecyrbold';
}

p {
  margin: 0;
  padding: 0 0 1.250em 0;
}

a {
  color: $main-link-color;
  text-decoration: underline;

  &:hover {
    text-decoration: none;
  }
}

.color-green {
  color: $color-green;
}
.color-gold {
  color: $color-gold;
}
.color-red {
  color: $text-red;
}
.color-blue {
  color: $main-link-color;
}
.large-text {
  font-size: 150%;
}

.text-center {
  text-align: center;
}

.list-main {
  margin: 0;
  padding: 0 0 1.250em 0;
  list-style: none;

  li {
    padding: 0 0 0.588em 0;

    ul {
      margin: 0;
      padding: 0.438em 0 0 0.938em;
      list-style: none;
    }
  }
}

.table-main {
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: fixed;
    width: 100%;
}

.main {
  min-height: 100%;
  min-width: 320px;
  height: auto !important;
  height: 100%;
  position: relative;
}

.clear-fix {
  clear: both;
  height: 1px;
  font-size: 1px;
  line-height: 1px;
}

.push-right {
  float: right;
}
.push-left {
  float: left;
}

.modal.active {
    display: block !important;
}

.modal {
    position: fixed;
    width: 90%;
    height: 95%;
    background: white;
    left: 5%;
    top: 2.5%;
    border-radius: $border-radius-main;
    display: none;
    overflow: hidden;
    z-index: 99;
    & .modal-button {
      background: #dcdcdc;
      border-radius: $border-radius-main $border-radius-main 0 0;
      height: 47px;
      padding: 12px 0 0 0;
      cursor: pointer;
      box-sizing: border-box;
      text-align: center;
      &:after {
        content: '';
        display: inline-block;
        width: 24px;
        height: 24px;
        background: url('../img/svg/cross-gray.svg') no-repeat;
        background-size: cover;
      }
    }
    & .modal-content {
        padding: 0.938em;
        overflow-y: scroll;
        box-sizing: border-box;
        height: calc(100% - 47px);
        &.menu {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 92%;
        }
    }
    & .loading {
        font-size: 1.1em;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $text-grey;
    }
}

.faq-page .modal {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  left: 0;
  top: 0;
  display: none;
  overflow: hidden;
  z-index: 99;
  border-radius: 0;
  & .modal-button {
    z-index: 100000;
    position: absolute;
    right: 0;
    height: 55px;
    background: rgba(0,0,0,0.2);
    padding: 15px 15px 0 0;
    cursor: pointer;
    box-sizing: border-box;
    text-align: right;
    left: 0;
    &:after {
      width: 25px;
      height: 25px;
      background: url('../img/svg/cross-out.svg') no-repeat;
      background-size: cover;
    }
  }
  & .modal-content {
    padding: 0;
    height: 100%;
  }
}

#modal-video {
    width: 100%;
    height: calc(100vw*9/16);
    left: 0;
    border-radius: 0;
    top: calc(50% - (100vw*9/16) / 2);
}


.fullscreen-loading {
    position: fixed;
    height: 100%;
    width: 100%;
    background: white;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: .9;
    font-family: $font-medium;
    color: black;
    left: 0;
    top: 0;
}

.fullscreen-loading-black {
    opacity: 1;
    background: black;
    color: white;
    display: none;
}

.metro-circle {
    content: '';
    width: 0.500em;
    height: 0.500em;
    border-radius: 100%;
    position: absolute;
    top: 1.1em;
    left: 1.357em;
    background: #293694;
}

#map {
    width: 100%;
    height: 100%;
}

#modal-map {
    & .modal-content {
        padding: 0;
    }
}

a[href^="http://maps.google.com/maps"],
a[href^="https://maps.google.com/maps"],
a[href^="https://www.google.com/maps"] {
    display: none !important;
}

.gmnoprint, .gm-style-cc {display:none !important}

.video-close-fix {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 104;
}

#youtube-video {
    height: 1px;
    width: 1px;
    opacity: 0;
    position: fixed;
    left: 50%;
    top: 50%;
}

.faq-page {
    & ul {
        margin-bottom: 1.8em;
    }
}

.back-arrow {
    & img {
        height: 14px;
        margin-right: 5px;
    }
    text-decoration: none;
    display: flex;
    align-items: flex-start;
    font-family: $font-regular;
    margin-bottom: 11px;
}

.b-like {
    display: block;
    font-family: $font-bold;
}

.right-img-price {
    font-size: 1.5em;
}

.select-dropdown {
    background: url('../img/svg/dropdown-gray.svg');
    width: 1.2em;
    height: 1.2em;
    background-size: 1.2em;
    border: 0;
    position: absolute;
    z-index: 19;
    right: .7em;
    top: .9em;
}

.text-gray {
    color: $text-grey;
}

.button-review-more {
    margin-top: $footer-margin;
}

.page-not-found-wrapper {
    min-height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    & .page-not-found {
        text-align: center;
        & h1 {
            font-family: $font-light;
        }
        & ul {
            list-style: none;
            padding: 0;
        }
    }
}

.text-success {
    color: $color-green;
}

.order-subjects-list {
    padding: .8em 1em 0;
    display: flex;
    align-items: center;
    &:first-child {
        margin-top: 1.2em;
    }
    & input {
        margin-right: .5em;
    }
}

.multiselect-popup {
    & .button-green-large {
        margin-top: 1em;
    }
}

.external-schedule-wrapper {
  img {
    width: 100%;
  }
}